import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Layout from "../../../layout/DefaultLayout";
import ContentBox from "../../../layout/ContentBox";
import Table from "../../../components/Table";
import Input from "../../../components/Input";
import Button from "../../../components/Button";

import MuiAlert from "../../../components/MuiAlert";

function ChangePassword() {
   const navigate = useNavigate();

  const [errorMessages, setErrorMessages] = useState({
    currentPasswordError: "",
    newPasswordError: "",
    confirmPasswordError: "",
  });

  const handlePasswordUpdate = () => {
    const currentPassword = ""; 
    const newPassword = ""; 
    const confirmPassword = {newPassword}; 
  
    const newErrorMessages = {
      currentPasswordError: currentPassword !== "1234" ? "*     ." : "",
      newPasswordError: newPassword.length < 4 || newPassword.length > 20 ? "*  4~20  .<br/><br/>*    ." : "",
      confirmPasswordError: newPassword !== confirmPassword ? "*   ." : "",
    };
  
    setErrorMessages(newErrorMessages);
  };

  const [open, setOpen] = useState(false);
  const [open02, setOpen02] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen02 = () => {
    setOpen(false);
    setOpen02(true);
  };
  const handleClose02 = () => {
    setOpen02(false);
  };

  return (
    <Layout>
      <div className="align mb_20">
        <h1 className="headline2"> </h1>
      </div>
      <hr className="primary" />
      <ContentBox>
        <div className="admin_myprofile">
          <Table
            globalClass="changepasswordbox"
            colgroup={[<col key="col1" width="20%" />, <col key="col2" />]}
          >
            <tr key="currentPassword">
              <th> </th>
              <td>
                <Input
                  placeholder="  "
                  shape="round"
                  type="password"
                />
                {errorMessages.currentPasswordError && (
                  <div className="error_txt mt_5 ml_5">
                    {errorMessages.currentPasswordError}
                  </div>
                )}
              </td>
            </tr>
            <tr key="newPassword">
              <th> </th>
              <td>
                <Input
                  type="password"
                  shape="round"
                  placeholder="4~20 ,    "
                  maxLength={20}
                />
                {errorMessages.newPasswordError && (
                  <div
                    className="error_txt mt_5 ml_5"
                    dangerouslySetInnerHTML={{
                      __html: errorMessages.newPasswordError,
                    }}
                  />
                )}
              </td>
            </tr>
            <tr key="confirmPassword">
              <th> </th>
              <td>
                <Input
                  type="password"
                  shape="round"
                  placeholder="  "
                />
                {errorMessages.confirmPasswordError && (
                  <div className="error_txt mt_5 ml_5">
                    {errorMessages.confirmPasswordError}
                  </div>
                )}
              </td>
            </tr>
          </Table>
          <div className="align end mt_20">
            <Button
              onClick={() => {
                handlePasswordUpdate();
                handleOpen();
              }}
              btntype="c11"
              size="xlarge"
            >
              
            </Button>
          </div>
        </div>
        <MuiAlert
          open={open}
          onClose={handleClose}
          title={<> ?</>}
          button={
            <>
              <Button onClick={handleClose} line>
                
              </Button>
              <Button onClick={handleOpen02} border="point">
                
              </Button>
            </>
          }
        />
        <MuiAlert
          open={open02}
          onClose={handleClose02}
          title={
            <>
               .
              <br />
               .
            </>
          }
          button={
            <>
              <Button
                onClick={() => navigate("/admin")}
                border="point"
              >
                
              </Button>
            </>
          }
        />
      </ContentBox>
    </Layout>
  );
}

export default ChangePassword;
